/* src/App.css */
/* ——— Reset 및 기본 스타일 ——— */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #fafafa;
  font-family: 'Pretendard', sans-serif;
  color: #333;
  line-height: 1.6;
}

/* ——— App 컨테이너 ——— */
.App {
  padding: 80px 20px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* ——— 헤더 & 타이틀 ——— */
header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 60px;
}

.App h1 {
  font-size: 2.8rem;
  font-weight: 800;
  color: #1a1a1a;
  letter-spacing: -0.5px;
  text-align: center;
  flex-grow: 1;
}

/* 홈 링크 */
.home-link {
  text-decoration: none;
  color: inherit;
  transition: color 0.3s ease;
}
.home-link:hover {
  color: #2c3e50;
}

/* ——— 햄버거 버튼 & 드롭다운 ——— */
/* 버튼 컨테이너 */
.dropdown {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

/* 햄버거 버튼 */
.dropbtn {
  background: transparent;
  border: none;
  padding: 8px;
  cursor: pointer;
  position: relative;
  z-index: 100;
}

/* 햄버거 3줄 */
.menu-icon {
  display: block;
  width: 24px;
  height: 2px;
  background: #333;
  position: relative;
  transition: background 0.3s ease;
}
.menu-icon::before,
.menu-icon::after {
  content: '';
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  background: #333;
  transition: transform 0.3s ease, background 0.3s ease;
}
.menu-icon::before { top: -8px; }
.menu-icon::after  { top:  8px; }

/* 호버/포커스 시 컬러 변화 */
.dropbtn:hover .menu-icon,
.dropbtn:focus .menu-icon,
.dropbtn:hover .menu-icon::before,
.dropbtn:focus .menu-icon::before,
.dropbtn:hover .menu-icon::after,
.dropbtn:focus .menu-icon::after {
  background: #2c3e50;
}

/* 드롭다운 메뉴 */
.dropdown-content {
  position: absolute;
  top: 100%;
  right: 0;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 8px 24px rgba(0,0,0,0.15);
  display: none;
  flex-direction: column;
  padding: 8px 0;
  margin-top: 8px;
  min-width: 140px;
  opacity: 0;
  transform: translateY(-10px);
  transition: opacity 0.3s ease, transform 0.3s ease;
  z-index: 50;
}

/* 메뉴 열렸을 때 */
.dropdown-content.show {
  display: flex;
  opacity: 1;
  transform: translateY(0);
}

.dropdown-content a {
  padding: 10px 16px;
  color: #333;
  text-decoration: none;
  font-weight: 500;
  transition: background 0.2s ease, color 0.2s ease;
}
.dropdown-content a:hover {
  background: #f0f0f0;
  color: #2c3e50;
}

/* ——— 포트폴리오 컨테이너 ——— */
.port-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* ——— 포트폴리오 카드 – 바운스 호버 애니메이션 ——— */
.port-card {
  position: relative;
  display: flex;
  width: 100%;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.05);
  overflow: hidden;
  will-change: transform, box-shadow;
}

/* 호버 인 애니메이션 */
.port-card.hovered {
  z-index: 10;
  animation: hoverBounceIn 0.5s forwards;
}
/* 호버 아웃 애니메이션 */
.port-card.not-hovered {
  animation: hoverBounceOut 0.4s forwards;
}

@keyframes hoverBounceIn {
  0%   { transform: translateY(0) scale(1); }
  60%  { transform: translateY(-18px) scale(1.04); }
  100% { transform: translateY(-12px) scale(1.02); }
}
@keyframes hoverBounceOut {
  0%   { transform: translateY(-12px) scale(1.02); }
  100% { transform: translateY(0) scale(1); }
}

/* 비디오 */
.port-video video {
  width: 280px;
  height: 180px;
  object-fit: cover;
  transition: filter 0.3s ease;
}
.port-card.hovered .port-video video {
  filter: brightness(1.05) contrast(1.05);
}

/* 정보 영역 */
.port-info {
  padding: 20px;
  display: flex;
  flex-direction: column;
  flex: 1;
  background: #fff;
}
.port-info h3 { font-size: 1.6rem; color: #222; margin-bottom: 8px; }
.port-info .date { font-size: 0.9rem; color: #777; margin-bottom: 6px; }
.port-info .desc { font-size: 1rem; color: #444; margin-bottom: 10px; }
.port-info .tech { font-size: 0.85rem; color: #666; margin-bottom: 20px; }

/* 링크 아이콘 */
.links {
  position: absolute;
  bottom: 15px;
  right: 15px;
  display: flex;
  gap: 12px;
}
.github-link img,
.deploy-link img {
  width: 28px;
  height: 28px;
  filter: grayscale(30%) opacity(0.7);
  transition: all 0.3s ease;
}
.github-link:hover img,
.deploy-link:hover img {
  filter: grayscale(0) opacity(1);
  transform: scale(1.15);
}

/* ——— Footer ——— */
.footer {
  text-align: center;
  padding: 40px 20px;
  background: #fff;
  border-top: 1px solid #eee;
  margin-top: 60px;
}
.footer h2 { font-size: 1.5rem; color: #222; margin-bottom: 10px; }
.footer p { font-size: 1rem; color: #555; margin: 5px 0; }
.email-link {
  color: #2c3e50;
  text-decoration: none;
  transition: color 0.3s ease;
}
.email-link:hover {
  color: #e74c3c;
}

/* ——— 반응형 ——— */
@media (max-width: 768px) {
  .port-container {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  .port-card {
    flex-direction: column;
  }
  .port-video video {
    width: 100%;
    height: 200px;
  }
  .App h1 {
    font-size: 2.2rem;
  }
}
